<template>
  <div>
    <title-bar :title-stack="titleStack" />
    <section>
      <div class="columns">
        <div class="column is-2">
          <div class="notification has-background-cwhite-2 padding-02">
            <div class="level">
              <div class="level-left">
                <div class="file is-cblue-3" style="zoom:70%">
                  <b-upload class="file-label" size="is-small" @input="importHistory">
                    <span class="file-cta">
                      <span class="file-label">导入</span>
                    </span>
                  </b-upload>
                </div>
              </div>
              <div class="level-right">
                <b-button size="is-small" type="is-cred-2" outlined @click="exportHistory()">
                  <p class="has-text-weight-bold">导出</p>
                </b-button>
              </div>
            </div>
            <p class="title is-6">历史释放列表:</p>
            <div v-for="(item, index) in allhistorys" :key="index">
              <div class="notification padding-02">
                <div class="level">
                  <div class="level-left">
                    <c-button variant-color="blue" variant="outline" size="sm" width="220px" @click="openHistory(item)">
                      {{ item.taskname }}
                    </c-button>
                  </div>
                  <div class="level-irght">
                    <c-button
                      left-icon="phone"
                      variant-color="green"
                      variant="outline"
                      size="sm"
                      width="30px"
                      @click="openIntoActive(item, index)"
                    ></c-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="column">
          <div v-if="selectdata !== undefined">
            <vue-json-pretty :data="selectdata" :deep="4" :virtual="true" />
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { exportHistoryFile } from '../../scripts/fileFunc'
import { CButton } from '@chakra-ui/vue'
import VueJsonPretty from 'vue-json-pretty'
import 'vue-json-pretty/lib/styles.css'
import TitleBar from '@/components/TitleBar'
export default {
  name: 'NowSprint',
  components: {
    TitleBar,
    VueJsonPretty,
    CButton
  },
  data() {
    return {
      allhistorys: [],
      selectdata: {}
    }
  },
  computed: {
    titleStack() {
      return ['RoWalock', '历史释放']
    }
  },
  mounted() {
    this.init()
  },

  methods: {
    /**
     * 初始化
     */
    init() {
      this.allhistorys = this.$store.state.releasemanagement.historyRelease // 赋值
    },
    /**
     * 打开历史记录
     */
    openHistory(item) {
      this.selectdata = item
    },
    /**
     * 导出历史记录
     */
    exportHistory() {
      const str = JSON.stringify(this.allhistorys)
      exportHistoryFile(str)
    },
    /**
     * 导入历史记录
     */
    importHistory(file) {
      const that = this // 定义
      // 新建一个FileReader
      const reader = new FileReader()
      // 读取文件
      reader.readAsText(file, 'UTF-8')
      // 读取完文件之后会回来这里
      reader.onload = function(e) {
        // 读取文件内容(赋值)
        const importcontent = e.target.result
        // 接下来可对文件内容进行处理
        that.$store.commit('uploadHistoryReleaseByFile', importcontent) // 提交
        that.init()
      }
    },
    /**
     * 重新放到活动的里面
     */
    openIntoActive(item, index) {
      // 接下来可对文件内容进行处理
      this.$store.commit('takeHistoryIntoActive', { item: item, index: index }) // 提交
    }
  }
}
</script>

<style>
.padding-02 {
  padding: 0.2rem;
}
</style>
